<template>
  <div class="project-page">
    <div class="project-page__back-button">
      <router-link :to="{ path: '/work' }"> <b-icon-arrow-bar-left> </b-icon-arrow-bar-left> Back to portfolio
      </router-link>
    </div>

    <div class="project-page__title">
      <h1 class="mb-3">Data-driven Inventory Management @ AEMICS</h1>
    </div>

    <div class="mb-5">
      <b-button
        href="https://www.researchgate.net/publication/383001602_Data-driven_inventory_management"
        target="_blank" variant="primary">
        View Report
      </b-button>
    </div>
    
    <div class="project-page__text-block">

      <h4> Project Context </h4>
      <p>
        Manufacturing companies generate large volumes of operational data, yet much of this data remains underutilized
        in day-to-day decision-making. AEMICS is an electronics design and manufacturing firm based in Oldenzaal.
        Although the company collects inventory data, production logs, and machine records, this information is not
        being systematically analyzed. Without actionable insights, the organization faced challenges such as stock
        shortages, inefficient replenishment cycles, and limited visibility into historical inventory trends.
      </p>

      <p>
        Effective inventory management is essential for ensuring production continuity, reducing waste, and optimizing
        purchasing decisions. This project explores how data analytics can be used to transform raw inventory data into
        meaningful insights that directly support operational efficiency.
      </p>

      <h4>Data-Driven Inventory Insights</h4>
      <p>
        The core objective of this project was to design and build a tool that enables AEMICS to extract value from its
        internal data. By leveraging analytics and visualization techniques, the goal was to improve the accuracy,
        transparency, and responsiveness of inventory management across the organization.
      </p>
      <p>
        Examples of the value hidden in the data include early warnings for components nearing stock depletion,
        visibility into component consumption during manufacturing, and automated triggers that support faster and more
        reliable decision-making. Unlocking these insights required a structured and scalable solution that integrates
        seamlessly with existing operational systems.
      </p>

      <h4> Approach </h4>
      <p>
        To deliver this solution, the project followed the Scrum framework, enabling iterative development and
        continuous feedback from stakeholders. The result is a production-ready application called Inventory Analyzer,
        tailored to AEMICS' data landscape and operational needs.
      </p>
      <p>
        The Inventory Analyzer provides:
      <ul>
        <li> Current inventory levels across all stocked components </li>
        <li> Historical inventory trends for understanding patterns </li>
        <li> Forecasted inventory changes based on production plans </li>
        <li> Minimum Kanban quantities to guide replenishment </li>
        <li> Remaining components on production process sheets for monitoring ongoing manufacturing </li>
      </ul>
      </p>

      <p>
        To ensure smooth integration into the company workflow, the application includes a dedicated API that allows the
        ERP system to request analytical results on demand. ERP users receive instant visualizations and insights
        generated directly from the Inventory Analyzer, creating a fully automated information loop.
      </p>

      <h4> Pipeline </h4>
      <p>
        The end-to-end pipeline developed for this project includes:

      <ol>
        <li> <strong> Data collection and cleaning </strong> from existing AEMICS systems </li>
        <li> <strong> Analytical models </strong> for forecasting and Kanban threshold estimation </li>
        <li> <strong> Interactive dashboards and visualizations</strong> for inventory insights</li>
        <li> <strong> REST API integration </strong> enabling direct ERP communication </li>
      </ol>

      This modular design ensures the system is adaptable to future requirements.
      </p>

      <h4> Outcome </h4>
      <p>
        Since deployment, the Inventory Analyzer has been actively used across the organization, including purchasers,
        the production team, the operations manager, and the CEO. Purchasers rely heavily on the forecasting module to
        anticipate shortages and make informed procurement decisions. Production teams use the tool to monitor component
        availability and reduce delays, while management uses historical trends to support strategic planning.
      </p>

      <p>
        By transforming raw data into operational intelligence, the application significantly enhances inventory
        visibility, automates manual processes, and supports proactive decision-making throughout AEMICS.
      </p>

      <img
        src="../../assets/img/portfolio/graduation-assignment/3.png"
        width="90%"
      />

